.bottom-panel {
    width: 1200px;
    margin: 0 auto;
    padding: 13px 59px 0 59px;
    box-sizing: border-box;
    position: relative;
    display: flex;
    align-items: center;
    background-image: url(../image/main.png);
    background-position: center;
    background-repeat: no-repeat;
    .coin {
        width: 95px;
        height: 50px;
        padding: 9px 0 13px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 15px;
        letter-spacing: 2px;
        font-weight: bold;
        box-sizing: border-box;
        background-image: url(../image/coin.png);
        background-size: contain;
        background-repeat: no-repeat;
        .coinsign {
            width: 30px;
            height: 50%;
            background-image: url(../image/coinsign.png);
            background-size: contain;
            background-repeat: no-repeat;
        }
        .coincounter {
            height: 50%;
            color: #FFE500;
        }
    }
    .winbox {
        width: 398px;
        height: 93px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 50px;
        letter-spacing: 2px;
        font-weight: bold;
        color: #FFE500;
        background-image: url(../image/winbox.png);
        background-size: contain;
        background-repeat: no-repeat;
    }       
}
